.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.custom-nav {
  position: absolute;
  bottom: 0px;
  left: 0px;
  right: 0px;
  z-index: 99;
  height: 100px;
}

.fix-navbar .custom-nav {
  position: fixed;
  top: 0px;
}

.fix-navbar .custom-nav .navbar {
  background-color: white;
}

.name-container {
  height: 250px;
  background-color: #e9ecef;
  margin-bottom: 40px;
  position: relative;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-align: center;
      align-items: center;
}



